* {
    box-sizing: border-box;
}

body {
    font-family: "Karla", sans-serif;
    margin: 0;
}
.header{
  display: flex;
  align-items: center;
  background: linear-gradient(90deg, #672280 0, #A626D3 100%);
  color: white;
  height: 65px;
  padding: 15px;
}
.logo{
  width: 40px;
 
}
.title
{
  font-size: 21px;
  font-weight: bold;
  font-family: karla,sans-serif;
  letter-spacing: -1px;
}
.sub-title{
  margin-left: auto;
  font-size: 12px;
  font-weight: normal;
  font-family: Inter,sans-serif;
}
main{
  padding: 36px;
}
.meme{
  display: grid;
  grid-template: 40px 40px /1fr 1fr;
  gap: 17px;
}

label{
  font-size: medium;

}

.meme-topText, .meme-bottomText{
    margin-top: 5px;
    color: #6B7280;
    font-size: 1rem;
    font-family: karla;
    padding: 10px;
    border-radius: 5px;
    border: 2px solid #D1D5DB;
    width: 100%;
    height: 35px;
}

.meme-btn{
  color: #fff;
  grid-column: 1/-1;
  border-radius: 5px;
  border: 0;
  outline: 0;
  background: linear-gradient(#711F8D, #A818DA);
  font-size: 16px;
  font-family: karla;
  font-weight: bold;
  letter-spacing: -1px;
  height: 40px;
  margin: 15px 0;
 
}
.memeData{
  position: relative;
  margin-top: 30px;
}
  
.meme-Image{
  
  width: 100%;
  border-radius: 5px;
 
 
}
.memeUpperText{
  font-family: Impact;
  font-size: 2rem;
  width: 100%;
  position: absolute;
  color:#fff;
  top: 0;
  text-transform: uppercase;
  text-align: center;
  text-shadow: 2px 2px 0 #000,
              -2px -2px 0 #000,
              2px -2px 0 #000,
              -2px 2px 0 #000,
              2px 0px 0 #000,
              0px 2px 0 #000,
              -2px 0 0 #000,
              0 -2px 0 #000,
              2px 2px 5px #000;
 
  
}
.memeLowerText{
  font-size: 2rem;
  font-family: Impact;
  width: 100%;
  position: absolute;
  color:#fff;
  bottom: 0;
  text-transform: uppercase;
  text-align: center;
  text-shadow: 2px 2px 0 #000,
              -2px -2px 0 #000,
              2px -2px 0 #000,
              -2px 2px 0 #000,
              2px 0px 0 #000,
              0px 2px 0 #000,
              -2px 0 0 #000,
              0 -2px 0 #000,
              2px 2px 5px #000;
              
}

